.zoom-level(normal){
	@media (max-width: 767px){
		.zoom-standard(115%, 85%, 708px);
	}
}
.zoom-level(large){
	.zoom-standard();
	@media (max-width: 767px){
		.zoom-standard(130%, 76%, 800px);
	}
}
.zoom-level(extra_large){
	.zoom-standard(135%, 74%, 811px);
	@media (max-width: 767px){
		.zoom-standard(145%, 65%, 900px);
	}
}

.zoom-normal{
	.zoom-level(normal);
}
.zoom-large{
	.zoom-level(large);
}
.zoom-extra-large{
	.zoom-level(extra_large);
}


