/*IE11 .zoom-large,.zoom-extra-largeå­—ä½“æ”¾å¤§éœ€è¦è¯¥æ ·å¼*/
@media screen and(-ms-high-contrast:active),(-ms-high-contrast:none) {
	html{
		height:100%;
	}
}

/*IE11 CSS hack*/
_:-ms-fullscreen,
:root .zoom-large,.zoom-extra-large{
	overflow:hidden;
	.app-wrapper{
		position:relative!important;
	}
}

/*IE11 CSS hack*/
_:-ms-fullscreen,
:root .zoom-large{
	margin-right: -7px;
	margin-bottom: -7px;
	.app-wrapper{
		width: 85.2%;
		height: 85.2%;
	}
	&.zoom-setting-item{
		width: 85.2%;
		margin-bottom: 0px;
	}
}

/*IE11 CSS hack*/
_:-ms-fullscreen,
:root .zoom-extra-large{
	margin-right: -2px;
	margin-bottom: -2px;
	.app-wrapper{
		width: 74%;
		height: 74%;
	}
	&.zoom-setting-item{
		width: 74%;
		margin-bottom: 0px;
	}
}

